.about_page {
    width: 100%;
    max-height: 700px;
    text-align: center;
    display: flex;
    justify-content: space-between;
    animation: pop 1s ease-out;
  }
  
  .image {
    width: 30%;
    height: auto;
    padding-left: 2.5%;
  }
  
  .profil_image {
    width: 400px;
    height: 400px;
    border-radius: 100%;
    box-shadow: 10px 5px 10px rgba(146, 232, 230, 0.3);  }
  
  .description {
    width: 70%;
    height: fit-content;
    margin-right: 10%;
    text-align: justify;
    color: black;
    padding: 2%;
    width: 50%;
    box-shadow: 0px 15px 40px 15px rgba(0, 0, 0, 0.5);
    overflow: auto;
    animation: pop 1.5s ease-out;
  }
  
  @keyframes pop {
    from {
      opacity: 0;
      transform: translateX(-10rem);
    }
    to {
      opacity: 1;
      transform: translateX(0rem);
    }
  }
  
  .title {
    align-items: center;
    font-size: xx-large;
    color: white;
  }
  
  .presentation {
    margin: auto;
    height: fit-content;
    color: black;
    padding: 2%;
    font-size: larger;
    font-family: "Times New Roman", Times, serif;
    background-image: linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0.9),
      rgba(51, 195, 228, 0.7)
    );
    border-top-right-radius: 80px;
    border-bottom-right-radius: 80px;
  }
  
  @media only screen and (max-width: 768px) {
    .about_page {
      margin-left: 50%;
      width: 700px;
      text-align: center;
      display: inline-block;
      justify-content: center;
    }
  
    .profil_image {
      width: 200px;
      height: 200px;
      margin-left: 35%;
      margin-top: 20%;
    }
    .description {
      max-height: 500px;
    }
  }
  
  @media only screen and (max-width: 420px) {
    .about_page {
      margin-left: 320px;
      width: 700px;
      text-align: center;
      display: inline-block;
      justify-content: center;
    }
  
    .profil_image {
      width: 200px;
      height: 200px;
      margin-left: 35%;
      margin-top: 25%;
    }
    .description {
      max-height: 500px;
      font-size: smaller;
    }
  }