.contactPage {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-around;
    animation: pop 1s ease-in-out;
  }
  
  @keyframes pop {
    from {
      opacity: 0;
      transform: translateY(-10rem);
    }
    to {
      opacity: 1;
      transform: translateY(0rem);
    }
  }
  
  .image {
    width: 30%;
    height: 600px;
    margin-left: 2%;
    background-image: linear-gradient(
        rgba(255, 255, 255, 0.1),
        rgba(0, 0, 0, 0.4)
      ),
      url("../../Images/Stand_1.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 12px;
    box-shadow: 0px 15px 40px 15px rgba(0, 0, 0, 0.5);
  }
  
  .contact_me {
    width: 60%;
    height: fit-content;
    text-align: center;
    animation: pop 1.5s ease-in-out;
  
  }
  
  .contact_me_section {
    width: 80%;
    min-width: 400px;
    height: 550px;
    margin-left: 2%;
    padding: 2%;
    display: flex;
    justify-content: left;
    box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.5);
  }
  
  .form_main_box {
    width: 80%;
    text-align: center;
  }
  
  .form_section {
    height: fit-content;
    min-height: 400px;
  }
  
  .name {
    padding: 1%;
  }
  
  .name_input {
    width: 100%;
    min-height: 25px;
    padding: 1%;
  }
  
  .email {
    padding: 1%;
  }
  
  .email_input {
    width: 100%;
    min-height: 25px;
    padding: 1%;
  }
  
  .message {
    padding: 1%;
  }
  
  .message_input {
    width: 100%;
    padding: 1%;
    min-height: 200px;
    line-height: 25px;
  }
  
  .button_section {
    text-align: center;
    color: white;
    width: 150px;
    height: fit-content;
    margin: 5% 0% 5%;
    font-size: x-large;
    border: 2px black solid;
    border-radius: 12px;
    background-color: rgb(0, 0, 0);
    transition: transform 0.2s ease;
    box-shadow: 0px 5px 20px 5px rgba(63, 63, 63, 0.5);
  }
  button:hover {
    transform: scale(1.2);
  }
  
  .media {
    text-align: center;
    font-size: x-large;
    display: flex;
    justify-content: space-around;
  }
  
  .contactPage_media {
    width: fit-content;
    height: fit-content;
    box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.5);
    margin: 1%;
    padding: 5%;
    transition: all 0.5s ease-in-out;
    margin-top: 20%;
  }
  
  .contactPage_media:hover {
    box-shadow: 0px 15px 40px 15px rgba(0, 0, 0, 0.5);
    cursor: pointer;
  }
  
  .info_section {
    width: 50%;
    height: fit-content;
    padding: 2%;
    display: inline;
  }
  
  .info {
    height: 50%;
  }
  
  .info_email {
    padding-top: 12%;
  }
  
  .info_email_text {
    margin-top: 10%;
  }
  
  .info_location {
    margin-top: 25%;
  }
  
  .info_location_text {
    margin-top: 10%;
  }
  
  .socialPlace {
    padding-top: 5px;
  }
  
  @media only screen and (max-width: 720px) {
    .image {
      min-width: 435px;
      height: 280px;
      margin: 1%;
    }
  
    .media {
      font-size: medium;
      display: inline-block;
      justify-content: center;
    }
  
    .contact_me_section {
      min-width: 420px;
    }
  }
  
  @media only screen and (max-width: 420px) {
    .contactPage {
      display: inline-block;
      justify-content: center;
    }
  
    .image {
      min-width: 435px;
      height: 280px;
      margin: 1%;
    }
  
    .form_main_box {
      max-width: 200px;
    }
  
    .info_section {
      max-width: 180px;
    }
  
    .media {
      font-size: medium;
    }
  
    .contact_me_section {
      min-width: 420px;
    }
  
    .info_email_text {
      font-size: xx-small;
    }
  
    .contactPage_media {
      display: inline-block;
      justify-content: center;
    }
  
    .image {
      margin-top: 25%;
      margin-right: 0%;;
    }
  }