.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.80);
  }
  
  .modal {
    position: fixed;
    top: -10%;
    left: 5%;
    width: 80%;
    background-color: white;
    padding: 1rem;
    border-radius: 14px;
    box-shadow: 15px 14px 33px rgba(255, 255, 255, 0.642);
    z-index: 2;
    animation: slide-up 1s ease-out forwards;
  }
  
  .header {
    background: #630303;
    padding: 1rem;
  }
  
  .header h2 {
    margin: 0;
    color: rgba(255, 255, 255);
  }
  
  .image_section {
      height: 100%;
      max-height: 800px;
  }
  
  .image {
      width: 100%;
      height: 50%;
      max-height: 600px;
  }

  .message {
    max-height: 600px;
    overflow:auto;
  }
  
  @media (min-width: 768px) {
    .modal {
      width: 50%;
      left: 25%;
    }
  }
  
  @keyframes slide-up {
    from {
      opacity: 0;
      transform: translateY(50rem);
    }
    to {
      opacity: 1;
      transform: translateY(15rem);
    }
  }