.side_menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5%;
    background-color: transparent;
    padding-top: 10px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  
  .side_menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: right;
    align-items: center;
    width: 100%;
    height: 100%;
  }
  
  .side_menu li {
    margin-left: 2%;
    margin-right: 2%;
    font-size: larger;
  }
  
  @media only screen and (max-width: 420px) {
    .side_menu li {
      margin-left: 2%;
      margin-right: 2%;
      font-size: small;
    }
  }